@keyframes startPageShowTextBox{from{transform:translateY(-10px) scale(0.8);opacity:0}to{opacity:1}}.logo-container{width:100%;height:300px;position:relative}.logo-container.first-animation .circle{animation:showCircleFirst 1.5s ease-in-out forwards}.logo-container.first-animation img{animation:showThumbnailFirst 1.5s ease-in-out forwards}.logo-container::before{content:'';display:block;padding-bottom:100%}.logo-container .circle{width:462px;height:462px;position:absolute;right:50%;bottom:0;transform:translateX(50%);border-radius:50%;background-color:var(--white-color);transform-origin:center;animation:showCircle 1.5s ease-in-out forwards}@keyframes showCircle{from{transform:translateX(-150px) translateY(-300px);opacity:0}to{transform:translateX(50%) translateY(0px);opacity:1}}@keyframes showCircleFirst{from{transform:translateX(50%) scale(5);opacity:0}to{transform:translateX(50%) scale(1);opacity:1}}.logo-container>img{position:absolute;right:50%;bottom:24px;width:224px;height:205px;animation:showThumbnail 1.5s ease-in-out forwards}@keyframes showThumbnail{from{transform:translateX(-150px) translateY(-300px) scale(0);opacity:0}to{transform:var(--transform, translateX(50%) translateY(0px));opacity:1}}@keyframes showThumbnailFirst{from{transform:translateX(50%) translateY(-300px) scale(0);opacity:0}to{transform:var(--transform, translateX(50%) translateY(0px));opacity:1}}@keyframes showLogoContainerFirst{from{width:500%;opacity:0}to{width:110%;opacity:1}}@keyframes showLogoContainer{from{width:0%;opacity:0}to{width:110%;opacity:1}}
