@keyframes startPageShowTextBox{from{transform:translateY(-10px) scale(0.8);opacity:0}to{opacity:1}}.chat-item{width:100%;height:64px;display:flex;flex-direction:row;align-items:center;justify-content:flex-start;border-radius:10px;padding:8px;user-select:none;-webkit-user-drag:none}.chat-item:not(:first-child){margin-top:12px}.chat-item:active{background:#f5f5f5}.chat-item>img{border-radius:50%;object-fit:cover;object-position:center}.chat-item .info-container{flex:1;margin-left:16px}.chat-item .info-container span{font-size:14px;line-height:17px;color:#33bee7}.chat-item .info-container p{margin-top:8px;font-size:12px;line-height:18px;color:#020e12}.chat-item .info-container p.unseen{font-weight:500}.chat-item .message-info-container{display:flex;flex-direction:column;align-items:flex-end;justify-content:flex-start}.chat-item .message-info-container .time{font-size:12px;line-height:14px;color:#333333;opacity:0.6}.chat-item .message-info-container .un-seen-count{display:flex;align-items:center;justify-content:center;width:22px;height:22px;background:#33bee7;border-radius:50%;font-weight:500;font-size:10px;line-height:14px;color:#ffffff;padding:4px;margin-top:6px}
