@keyframes startPageShowTextBox{from{transform:translateY(-10px) scale(0.8);opacity:0}to{opacity:1}}.start-page-layout{width:100vw;height:100vh;display:flex;flex-direction:column;align-items:flex-start;justify-content:flex-start;background-color:var(--primary-color);overflow-x:hidden}.start-page-layout .body{width:100%;flex:1;z-index:1}.start-page-layout .body.hide{transform:translateX(-300px) scale(0.5);transition:all ease-in-out 500ms}.start-page-layout .footer{width:100%;height:20px;display:flex;flex-direction:row;align-items:center;justify-content:space-between;padding-inline:42px;margin-bottom:48px;z-index:2}.start-page-layout .footer .step>span{width:10px;height:10px;display:inline-block;border-radius:100rem;background:rgba(255,252,242,0.75);transition:all ease 300ms}.start-page-layout .footer .step>span:not(:first-child){margin-left:10px}.start-page-layout .footer .step>span.active{width:26px;height:10px;background-color:var(--white-color)}.start-page-layout .footer>button{display:flex;align-items:center;justify-content:center;color:#fafafa;font-family:'Roboto', sans-serif;font-size:14px;font-weight:500;line-height:20px;background-color:transparent}
