@keyframes startPageShowTextBox{from{transform:translateY(-10px) scale(0.8);opacity:0}to{opacity:1}}.toast-message-item{width:100%;display:flex;flex-direction:row;align-items:center;justify-content:flex-start;position:relative;border-radius:12px;padding:var(--padding);background-color:var(--white-color);box-shadow:0 0 10px 0 rgba(0,0,0,0.2);transition:all ease-in-out 500ms;transform-origin:top left;animation:showToastMessage 500ms ease-in-out}.toast-message-item.closing{transform:translateX(100%) scaleY(0);opacity:0}.toast-message-item:not(:first-child){margin-top:10px}.toast-message-item>.icon{width:18px;height:18px;margin-right:28px}.toast-message-item .content-container{width:100%;height:100%;flex:1}.toast-message-item .content-container .title{font-size:17px;font-weight:500;color:#444}.toast-message-item .content-container .content,.toast-message-item .content-container .time{font-size:14px;color:#444;opacity:0.8}.toast-message-item .content-container .content{margin-top:8px}.toast-message-item .content-container .time{margin-top:14px;display:block}.toast-message-item .close-btn{width:20px;height:20px;position:absolute;top:10px;right:10px;padding:10px;box-sizing:content-box;background-color:transparent;border-radius:50%;transition:all ease 300ms}.toast-message-item .close-btn:hover{background-color:#eee}@keyframes showToastMessage{from{opacity:0;transform:translateX(100%) scaleY(0)}to{opacity:1;transform:translateX(0%) scaleY(1)}}
