@keyframes startPageShowTextBox{from{transform:translateY(-10px) scale(0.8);opacity:0}to{opacity:1}}.slide-two{width:100%;height:100%;display:flex;flex-direction:column;align-items:center;justify-content:flex-start}.slide-two .text-box{opacity:0;animation:startPageShowTextBox 1s ease-in-out 500ms forwards;margin-top:150px}.slide-two .text-box>h2{display:flex;flex-direction:row;align-items:center;justify-content:center;color:var(--white-color);font-size:32px;font-weight:300}.slide-two .text-box>h2 svg{width:38px;height:38px;margin-right:14px}.slide-two .text-box>h2 span{position:relative;top:-2px}.slide-two .text-box p{max-width:300px;margin-top:16px;color:rgba(255,255,255,0.65);font-size:15px;line-height:21px;text-align:center}
