@keyframes startPageShowTextBox{from{transform:translateY(-10px) scale(0.8);opacity:0}to{opacity:1}}.filter-list{display:flex;flex-direction:row;align-items:center;justify-content:flex-start;gap:24px;margin-top:24px}.filter-list>li{padding:4px 8px}.filter-list>li.selected{background:#33bee7;border-radius:4px}.filter-list>li.selected p{color:var(--white-color);opacity:1}.filter-list>li p{font-size:12px;line-height:18px;color:#33bee7;opacity:0.5}
