@keyframes startPageShowTextBox{from{transform:translateY(-10px) scale(0.8);opacity:0}to{opacity:1}}.avatar-container{width:var(--size);height:var(--size);position:relative}.avatar-container>img{width:100%;height:100%;border-radius:50%;object-fit:cover;object-position:center}.avatar-container.active::before{content:'';width:25%;height:25%;position:absolute;right:0;bottom:0;display:block;border:2px solid var(--white-color);border-radius:50%;background:#4cb944}
