@keyframes startPageShowTextBox{from{transform:translateY(-10px) scale(0.8);opacity:0}to{opacity:1}}.slide-three{width:100%;height:100%;display:flex;flex-direction:column;align-items:center;justify-content:flex-start}.slide-three .circle{width:276px;height:276px;position:absolute;right:50%;bottom:-90px;transform:translateX(50%);background-color:var(--white-color);border-radius:50%;z-index:1;opacity:0;animation:showCircle 500ms 1s ease-in-out forwards}@keyframes showCircle{from{opacity:0}to{opacity:1}}.slide-three .text-box{opacity:0;animation:startPageShowTextBox 1s ease-in-out 500ms forwards;margin-top:150px}.slide-three .text-box h2{display:flex;flex-direction:row;align-items:center;justify-content:center;color:var(--white-color)}.slide-three .text-box h2 svg{width:35px;height:35px;margin-right:16px}.slide-three .text-box h2 span{font-size:36px;font-weight:300}.slide-three .text-box p{max-width:300px;color:rgba(255,255,255,0.65);font-size:16px;text-align:center;line-height:22px;margin-top:20px}
