@keyframes startPageShowTextBox{from{transform:translateY(-10px) scale(0.8);opacity:0}to{opacity:1}}.login-page{width:100vw;height:100vh;overflow:auto;background-color:var(--primary-color);padding-bottom:30px}.login-page .header-container{width:100%;height:200px;position:relative;overflow:hidden}.login-page .header-container .circle{width:462px;height:462px;position:absolute;right:50%;bottom:0;transform:translateX(50%);border-radius:50%;background-color:var(--white-color)}.login-page .header-container img{width:98px;height:90px;position:absolute;right:50%;bottom:50px;transform:translateX(50%)}.login-page .body{margin-top:60px}.login-page .body .form{width:320px;max-width:90vw;margin-inline:auto}.login-page .body .form .separate{margin-block:24px;color:var(--white-color);font-weight:400;font-size:16px;text-align:center}.login-page .body .form>input{width:100%;height:50px}.login-page .body .form input{border-radius:4px;border:2px solid transparent;padding-inline:10px}.login-page .body .form input:focus{border-color:#bebebe}.login-page .body .form input::selection{color:#000;background:rgba(255,187,0,0.548)}.login-page .body .form button[type='submit']{width:100%;height:50px;border-radius:4px;margin-top:20px;background-color:#f9c58d;transition:all ease-in-out 100ms}.login-page .body .form button[type='submit']:active{opacity:0.7}.login-page .body .form button[type='submit'] p{color:#333;font-size:16px;font-weight:500}.login-page .body .form .form-footer{width:100%;display:flex;flex-direction:row;align-items:center;justify-content:space-between;margin-top:50px}.login-page .body .form .form-footer a{color:var(--white-color);font-size:16px;font-weight:500;text-decoration:none}
