@keyframes startPageShowTextBox{from{transform:translateY(-10px) scale(0.8);opacity:0}to{opacity:1}}.slide-one{width:100%;height:100%;display:flex;flex-direction:column;align-items:center;justify-content:flex-start}.slide-one .text-box{opacity:0;animation:startPageShowTextBox 1s ease-in-out 500ms forwards;color:var(--white-color);text-align:center;margin-top:130px}.slide-one .text-box h2{font-weight:500;font-size:45px;line-height:54px}.slide-one .text-box p{font-size:20px;line-height:30px}
