@keyframes startPageShowTextBox{from{transform:translateY(-10px) scale(0.8);opacity:0}to{opacity:1}}.input-container{width:100%}.input-container>div{width:100%;height:100%;position:relative}.input-container>div input[type='text'],.input-container>div input[type='password'],.input-container>div input:not([type]){width:100%;height:40px;border:2px solid transparent;border-radius:4px;padding-inline:10px}.input-container>div input[type='text']:focus,.input-container>div input[type='password']:focus,.input-container>div input:not([type]):focus{border-color:#ccc}.input-container>div .toggle-password svg{position:absolute;top:50%;right:10px;width:18px;height:18px;transform:translateY(-50%);padding:10px;box-sizing:content-box;color:#555}.input-container .error-message{width:100%;display:flex;flex-direction:row;align-items:center;justify-content:flex-start;color:#ff2600;font-size:14px;margin-top:8px;overflow:hidden;animation:showErrorMessage 300ms ease-in-out forwards}.input-container .error-message svg{width:18px;height:18px;flex-shrink:0;margin-right:10px}.input-container .error-message span{display:-webkit-box;-webkit-box-orient:vertical;-webkit-line-clamp:2;overflow:hidden}@keyframes showErrorMessage{from{opacity:0;max-height:0}to{opacity:1;max-height:30px}}
