@keyframes startPageShowTextBox{from{transform:translateY(-10px) scale(0.8);opacity:0}to{opacity:1}}.separate{width:100%;height:36px;position:relative}.separate::before{content:'';width:24px;height:3px;position:absolute;top:8px;right:50%;transform:translateX(50%);background:#020e12;opacity:0.2;border-radius:100px;display:block}.header{display:flex;flex-direction:row;align-items:center;justify-content:space-between}.header .search-bar{width:100px;height:40px;border-radius:4px;border:1.5px solid #ccc;flex:1;padding-inline:10px}.header .search-bar:focus{border-color:#33bee7}.header>span{font-size:18px;line-height:21px;background:linear-gradient(90deg, #33bee7 20%, #7cd6f1 100%);-webkit-background-clip:text;-webkit-text-fill-color:transparent;background-clip:text}.header>svg{width:24px;height:24px;margin-left:10px;box-sizing:content-box;padding:4px;transition:all ease 200ms;border-radius:50%}.header>svg:active{background-color:#eee}.header>svg path{fill:#ccc}
