@keyframes startPageShowTextBox{from{transform:translateY(-10px) scale(0.8);opacity:0}to{opacity:1}}.toast-message{width:100%;max-height:100vh;position:fixed;top:0;right:0;padding:10px;overflow:hidden;z-index:2}.toast-message.hide{z-index:-1}.toast-message .toast-message-wrapper{max-width:inherit;max-height:inherit;padding:10px 10px 100px;overflow-y:auto}.toast-message .toast-message-wrapper .remove-all-message-button{width:100%;height:50px;display:flex;align-items:center;justify-content:center;border-radius:8px;background-color:var(--white-color);margin-top:10px;transition:all ease-in-out 200ms;box-shadow:0 0 10px 0 rgba(0,0,0,0.2)}.toast-message .toast-message-wrapper .remove-all-message-button:active{opacity:0.8}.toast-message .toast-message-wrapper .remove-all-message-button p{font-weight:500;font-size:16px;opacity:0.8}
