@keyframes startPageShowTextBox{from{transform:translateY(-10px) scale(0.8);opacity:0}to{opacity:1}}.chat-page-header{background-color:var(--white-color)}.chat-page-header .header-container{width:100%;display:flex;flex-direction:row;align-items:center;justify-content:space-between;padding:44px 16px 24px}.chat-page-header .header-container .tapo{display:flex;flex-direction:row;align-items:center;justify-content:flex-start}.chat-page-header .header-container .tapo img{width:28px}.chat-page-header .header-container .tapo span{font-size:18px;line-height:21px;background:linear-gradient(90deg, #33bee7 20%, #7cd6f1 100%);-webkit-background-clip:text;-webkit-text-fill-color:transparent;background-clip:text}.chat-page-header .header-container .user img{width:24px;height:24px;object-fit:cover;border-radius:50%}
