@keyframes startPageShowTextBox{from{transform:translateY(-10px) scale(0.8);opacity:0}to{opacity:1}}.pinned-chat-item{position:relative;padding:16px 16px 20px;background:#f7f7f7;border-radius:8px}.pinned-chat-item:active{opacity:0.7}.pinned-chat-item::before{content:'';width:18px;height:18px;position:absolute;top:8px;right:8px;background:var(--pin-icon);display:block}.pinned-chat-item:not(.seen){background:rgba(51,190,231,0.1);border-radius:8px}.pinned-chat-item:not(.seen)::before{width:12px;height:12px;background:#33bee7;border-radius:50%}.pinned-chat-item:not(.seen) .content{color:#33bee7}.pinned-chat-item .user-info{display:flex;flex-direction:row;align-items:center;justify-content:flex-start}.pinned-chat-item .user-info img{width:36px;height:36px;border-radius:50%;margin-right:8px}.pinned-chat-item .user-info span{display:-webkit-box;-webkit-box-orient:vertical;-webkit-line-clamp:2;overflow:hidden;font-size:14px;line-height:17px;color:#33bee7}.pinned-chat-item .content{display:-webkit-box;-webkit-box-orient:vertical;-webkit-line-clamp:1;overflow:hidden;font-size:12px;line-height:18px;color:#020e12;margin-top:12px}
