@keyframes startPageShowTextBox{from{transform:translateY(-10px) scale(0.8);opacity:0}to{opacity:1}}.chat-page-typing{width:100%;height:72px;position:fixed;right:0;bottom:0;display:flex;flex-direction:row;align-items:center;justify-content:flex-start;padding:8px 16px 28px;border-top:1px solid #ededed;background-color:var(--white-color);z-index:2}.chat-page-typing .form{width:100%;display:flex;flex-direction:row;align-items:center;justify-content:flex-start}.chat-page-typing .form>button{background-color:transparent}.chat-page-typing .form .emoji,.chat-page-typing .form .send-message-button{width:24px;height:24px;color:#ccc}.chat-page-typing .form .send-message-button{cursor:pointer;user-select:none;-webkit-user-drag:none}.chat-page-typing .form .send-message-button.active{color:#009dff}.chat-page-typing .form .send-message-button:active{animation:toggleMessageButon 500ms ease-in-out forwards}@keyframes toggleMessageButon{50%{transform:scale(1.5)}100%{transform:scale(1)}}.chat-page-typing .form .message-input{height:18px;flex:1;border:none;margin-left:16px}
