@keyframes startPageShowTextBox{from{transform:translateY(-10px) scale(0.8);opacity:0}to{opacity:1}}.register-page{width:100vw;height:100vh;overflow:auto;background-color:var(--primary-color);padding-bottom:30px}.register-page .header{width:100%;height:200px;position:relative;overflow:hidden}.register-page .header .circle{width:462px;height:462px;position:absolute;right:50%;bottom:0;transform:translateX(50%);border-radius:50%;background-color:var(--white-color)}.register-page .header img{width:98px;height:90px;position:absolute;right:50%;bottom:50px;transform:translateX(50%)}.register-page .body{width:320px;max-width:90vw;margin:40px auto 0}.register-page .body .title{color:var(--white-color);font-weight:500;text-align:center}.register-page .body .form>label{display:inline-flex;flex-direction:row;align-items:center;justify-content:flex-start;margin-top:20px;margin-right:30px}.register-page .body .form>label input{width:20px;height:20px}.register-page .body .form>label span{margin-left:10px;font-size:16px;line-height:20px}.register-page .body .form>button{width:100%;height:50px;border-radius:4px;margin-top:20px;background-color:#f9c58d;transition:all ease-in-out 100ms}.register-page .body .form>button:active{opacity:0.7}.register-page .body .form>button p{color:#333;font-size:16px;font-weight:500}.register-page .body .form .form-footer{margin-top:40px;text-align:right}.register-page .body .form .form-footer a{color:var(--white-color);text-decoration:none;font-size:16px;font-weight:500}
